@import url(//db.onlinewebfonts.com/c/b2e75c336df191233b3096682c9ee6be?family=PixelarRegularW01-Regular);
*{list-style-type:none;margin:0;font-family:'PixelarRegularW01-Regular';box-sizing:border-box;text-align:center}

.card{border:1px solid grey;border-radius:8px;width:100%;height:190px;margin:10px;padding:10px;display:flex;flex-direction:column;justify-content:space-evenly}.card .name{text-transform:capitalize;font-size:24px}.card .types_container{border-top:1px solid grey;display:flex;padding:0}.card .types_container .types_item{background-color:grey;color:white;font-size:14px;border-radius:15px;margin:5px;padding:1px 8px 1px 8px}

.h1{font-size:45px}.pokeList{display:flex;flex-wrap:wrap;justify-content:space-evenly;align-items:center;padding:0}.pokeList .pokeList_item{width:150px}


